<template>
  <ul>
    <li v-for="data in list" :key="data.dt">
      {{ data.dt }}
      <ul>
        <li
          v-for="item in data.dd"
          :key="item.name"
          @click="handleClick(item.url)"
        >
          {{ item.name }}
        </li>
      </ul>
    </li>
  </ul>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      list: []
    }
  },
  mounted () {
    this.getList()
  },
  methods: {
    handleClick (url) {
      window.open(url)
    },
    getList () {
      axios({
        // url: '/ajax/test.json'
        url: '/test.json'
      }).then((res) => {
        this.list = res.data
      })
    }
  }
}
</script>

<style lang="less" scoped>
ul {
  display: grid;
  grid-template-columns: 100%;
  @media screen and (min-width: 576px) {
    grid-template-columns: 50% 50%;
  }
  @media screen and (min-width: 768px) {
    grid-template-columns: 33.3333% 33.3333% 33.3333%;
  }
  @media screen and (min-width: 992px) {
    grid-template-columns: 25% 25% 25% 25%;
  }
  li {
    height: 216px;
    margin-bottom: 16px;
    padding: 0 12px;
    font-size: 16px;
    /*1．先强制一行内显示文本*/
    white-space: nowrap; /*默认normal自动换行*/
    /*2．超出的部分隐藏*/
    overflow: hidden;
    /*3．文字用省略号替代超出的部分*/
    text-overflow: ellipsis;
    cursor: default;
    ul {
      grid-template-columns: 33.3333% 33.3333% 33.3333%;
      align-content: start;
      margin: 12px 0 8px 0;
      padding-top: 12px;
      height: 175px;
      background: rgba(255, 255, 255, 0.3);
      border-radius: 20px;
      overflow: auto;
      scrollbar-width: none;
      -ms-overflow-style: none;
      &::-webkit-scrollbar {
        display: none !important;
      }
      &:hover {
        background-color: rgba(255, 255, 255, 0.15);
      }
      li {
        height: auto;
        margin-bottom: 13px;
        font-size: 13px;
        line-height: 19px;
        white-space: normal;
        overflow: hidden;
        text-overflow: ellipsis;
        /*弹性伸缩盒子模型显示*/
        display: -webkit-box;
        /*限制在一个块元素显示的文本的行数*/
        -webkit-line-clamp: 2;
        /*设置或检索伸缩盒对象的子元素的排列方式*/
        -webkit-box-orient: vertical;
        cursor: pointer;
        &:hover {
          color: rgb(64, 169, 255);
        }
      }
    }
  }
}
</style>
