<template>
  <div>
    <h1>{{ title }}</h1>
    <div class="search-wrapper">
      <ul>
        <li
          v-for="(item, index) in searchList"
          :key="item.engine"
          @click="chooseEngine(index, item.url, item.placeholder)"
          :class="active === index ? 'active' : ''"
        >
          {{ item.engine }}
        </li>
      </ul>
      <div class="search">
        <div>
          <input
            type="text"
            v-model="test"
            @keyup.enter="handleSearch"
            @click="handleClick"
            @blur="handleBlur"
          />
          <span class="icon-search" @click="handleSearch"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '大白导航'
    }
  },
  data () {
    return {
      searchList: [
        {
          engine: '百度',
          url: 'http://www.baidu.com/s?wd=',
          placeholder: '百度一下，你就知道'
        },
        {
          engine: '必应',
          url: 'https://cn.bing.com/search?q=',
          placeholder: ' '
        }
      ],
      active: 0,
      engineUrl: 'http://www.baidu.com/s?wd=',
      test: '百度一下，你就知道'
    }
  },
  methods: {
    chooseEngine (index, engine, placeholder) {
      this.engineUrl = engine
      if (
        this.test === this.searchList[this.active].placeholder ||
        this.test === ''
      ) {
        this.test = placeholder
      }
      this.active = index
    },
    handleSearch () {
      window.open(this.engineUrl + this.test)
      this.test = ''
    },
    handleClick () {
      if (this.test === this.searchList[this.active].placeholder) {
        this.test = ''
      }
    },
    handleBlur () {
      if (this.test === '') {
        this.test = this.searchList[this.active].placeholder
      }
    }
  }
}
</script>

<style lang="less" scoped>
h1 {
  padding: 10.4vw 0 30px 0;
  cursor: default;
}
.search-wrapper {
  ul {
    display: flex;
    justify-content: center;
    font-size: 13px;
    li {
      padding: 10px;
      line-height: 18px;
      cursor: pointer;
    }
    .active {
      font-size: 17px;
    }
  }
  .search {
    display: flex;
    justify-content: center;
    padding: 20px 12px;
    div {
      width: 580px;
      position: relative;
      input {
        width: 100%;
        height: 40px;
        padding: 0 88px 0 15px;
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 25px;
        border: none;
        color: #fff;
        font-size: 14px;
        outline: none;
      }
      span {
        position: absolute;
        right: 0;
        width: 58px;
        height: 40px;
        border-radius: 0 25px 25px 0;
        line-height: 40px;
        cursor: pointer;
      }
    }
  }
}
</style>
